.user-profile__container{
    width: 85%;
    margin: auto;
}
.user-profile__container .list{
justify-content: space-between;
}

.header{
    display: flex;
    justify-content: space-between;
}
.header .clock{
    display: flex;
}




.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  padding: 1.875rem;
}

h1 {
  --h1-spacing: 3.125rem;
  padding-bottom: var(--h1-spacing);
  margin-top: 0;
  margin-bottom: var(--h1-spacing);
  font-weight: 300;
  color: inherit;
  border-bottom: 1px solid currentColor;
}
@media (max-width: 30em) {
  h1 {
    --h1-spacing: 1.875rem;
  }
}

.btn {
  --btn-color: #27374d;
  --btn-hover-color: #fff;
  --btn-bg: transparent;
  --btn-hover-bg: var(--btn-color);
  position: relative;
  padding: 0.9375em 1.875em;
  box-shadow: 0 0 0 0.0625em var(--btn-color) inset;
  border: 0;
  border-radius: 0.25em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: var(--btn-bg);
  color: var(--btn-color);
  cursor: pointer;
  transition: 0.225s ease-in-out;
  transition-property: box-shadow, background-color, color;
  height: 4rem;
}
.btn:focus-visible {
  box-shadow: none;
  background-color: var(--btn-hover-bg);
  color: var(--btn-hover-color);
}
@media (min-width: 48em) {
  .btn:hover {
    box-shadow: none;
    background-color: var(--btn-hover-bg);
    color: var(--btn-hover-color);
  }
}

.ripple {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.ripple-item {
  position: absolute;
  display: block;
  border-radius: 50%;
  background-color: currentColor;
  -webkit-animation: ripple-grow 0.65s ease-in-out forwards;
          animation: ripple-grow 0.65s ease-in-out forwards;
}

@-webkit-keyframes ripple-grow {
  from {
    opacity: 0.45;
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
}

@keyframes ripple-grow {
  from {
    opacity: 0.45;
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
}
.clock{
    margin: 0 2rem;
}